</section></template>

<script>
import PageMixin from '@/mixins/page'

export default {
  mixins: [
    PageMixin,
  ],
  data () {
    return {
      select1: false,
      change1: 0,
      table1: [
        {
          label: 'A',
          value: 3,
        }, {
          label: 'B',
          value: 1,
        }, {
          label: 'C',
          value: 2,
        }, {
          label: 'D',
          value: 4,
        },
      ],
      table2: [
        {
          label: 'A',
          value: 3,
        }, {
          label: 'B',
          value: 1,
        }, {
          label: 'C',
          value: 2,
        }, {
          label: 'D',
          value: 4,
        },
      ],
    }
  },
  methods: {
    SortChange (sort) {
      this.table2.sort(this.getCompare(sort))
    },
    Check (row) {
      console.log('check', row)
    },
    Delete (row) {
      console.log('delete', row)
    },
    OnChange () {
      this.table1.map(item => {
        item.value += 1
      })
    },
    getCompare (sort) {
      const prop = sort.prop, order = (sort.order === 'ASC')
      return (a, b) => {
        return (a[prop] < b[prop]) ^ order
      }
    }
  },
}
</script>


